import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../views/home'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: home
  },
  {
    path:'/',
    name:'主页面',
    redirect:home
  },
  {
    path: '/notfind',
    name: 'notfind',
    component: () => import(/* webpackChunkName: "about" */ '../views/notfind.vue')
  },
  {
    path: '/ios',
    name: 'ios',
    component: () => import(/* webpackChunkName: "about" */ '../views/ios.vue')
  },
  {
    path: '/android',
    name: 'android',
    component: () => import(/* webpackChunkName: "about" */ '../views/android.vue')
  }
]

const router = new VueRouter({
  mode:'hash',
  routes
})

export default router
