<template>
  <div id="home" :style="{ height: bodyHeight + 'px' }">
    <div class="bg">
      <div class="title"></div>
      <input
        type="text"
        class="phone"
        placeholder="请输入手机号"
        v-model="phone"
      />
      <button class="login" @click="Submit"></button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bodyHeight: "",
      modelPhone: "",
      dPmub: "",
      phone: "",
      exchangeCode: "",
      exchangeLink: "",
      parentDPmub: "",
      exist: 0,
    };
  },
  computed: {},
  methods: {
    Submit() {
      this.splitUrl();
      this.checkPhone(this.phone);
    },
    isModel() {
      let u = navigator.userAgent;
      let device = ""; //当前设备信息
      if (u.indexOf("Android") > -1 || u.indexOf("Linux") > -1) {
        //安卓手机
        device = "Android";
      } else if (u.indexOf("iPhone") > -1) {
        //苹果手机
        device = "iPhone";
      } else if (u.indexOf("Windows Phone") > -1) {
        //winPhone手机
        device = "Windows Phone";
      }
      this.modelPhone = device;
      console.log(device);
    },
    checkPhone() {
      if (this.phone == "") {
        this.$toast({
          type: "fail",
          message: "请输入手机号",
          duration: 800,
          className: "toastStyle",
        });
        // this.$notify({
        //   type: "warning",
        //   message: "手机号输入为空",
        //   duration: 1000,
        // });
      } else {
        let reg = /^[1][3,4,5,6,7,8][0-9]{9}$/;
        if (!reg.test(this.phone)) {
          // this.$notify({
          //   type: "warning",
          //   message: "手机号格式错误！",
          //   duration: 1000,
          // });
          this.$toast({
            type: "fail",
            message: "手机号格式错误",
            duration: 800,
            className: "toastStyle",
          });
        } else {
          this.login();
        }
      }
    },
    //登录
    login() {
      this.$axios({
        url: "http://h5.1des1.com.cn/api.php",
        method: "post",
        data: this.qs.stringify({
          phone: this.phone,
          parentDPmub: this.parentDPmub,
          isModel: this.modelPhone,
        }),
        headers: {},
      })
        .then((res) => {
          console.log(res);
          this.dPmub = res.data.DPmub;
          this.exchangeCode = res.data.ExchangeCode;
          this.exchangeLink = res.data.ExchangeLink;
          this.exist = res.data.exist;
          this.goPage();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //跳转页面
    goPage() {
      if (this.exist) {
        // this.$notify({
        //   type: "success",
        //   message: "登陆成功！",
        //   duration: 1000,
        // });
        this.$toast({
          type: "success",
          message: "登陆成功",
          duration: 1000,
          className: "toastStyle",
        });
        if (this.modelPhone == "iPhone") {
          this.$router.push({
            name: "ios",
            params: {
              exchangeCode: this.exchangeCode,
              exchangeLink: this.exchangeLink,
              dPmub: this.dPmub,
            },
          });
        } else {
          this.$router.push({
            name: "android",
            params: {
              exchangeCode: this.exchangeCode,
              exchangeLink: this.exchangeLink,
              dPmub: this.dPmub,
            },
          });
        }
      } else {
        this.$toast({
          type: "success",
          message: "手机号不在库",
          duration: 1000,
          className: "toastStyle",
        });
        // this.$notify({
        //   type: "success",
        //   message: "手机号不在库！",
        //   duration: 1000,
        // });
        this.$router.push("/notfind");
      }
    },
    //截取网址参数
    splitUrl() {
      if (window.location.href.split("?")[1]) {
        const params = window.location.href.split("?")[1].split("&");
        const obj = {};
        params.map((v) => (obj[v.split("=")[0]] = v.split("=")[1]));
        this.parentDPmub = obj["parentDPmub"];
      }
    },
  },
  mounted() {
    this.bodyHeight = document.documentElement.clientHeight;
    this.splitUrl();
    this.isModel();
  },
};
</script>

<style>
* {
  padding: 0;
  margin: 0;
}
#home {
  text-align: center;
  color: #2c3e50;
  position: relative;
}
#home .bg {
  width: 100vw;
  height: 100%;
  background: url(../static/images/bg2.jpg) no-repeat;
  background-size: 100% 100%;
}
#home .bg .title {
  width: 81.6vw;
  height: 35.5vw;
  background: url(../static/images/title1.png) no-repeat;
  background-size: 100% 100%;
  position: absolute;
  top: 60vw;
  left: 9.6vw;
}

#home .bg .phone {
  width: 64.8vw;
  height: 11.5vw;
  background: none;
  outline: none;
  border: none;
  background: url(../static/images/input.png) no-repeat;
  background-size: 100% 100%;
  position: absolute;
  top: 99vw;
  left: 17.9vw;
  line-height: 11.5vw;
  text-align: center;
  color: #000;
  font-size: 3.5vw;
  font-weight: 700;
  letter-spacing: 0.3vw;
}
#home ::-webkit-input-placeholder {
  color: #000;
  font-size: 3.5vw;
  font-weight: 700;
  text-align: center;
  letter-spacing: 0.8vw;
}
#home .bg .login {
  width: 65vw;
  height: 12vw;
  background: url(../static/images/btn1.png) no-repeat;
  background-size: 100% 100%;
  position: absolute;
  top: 113vw;
  left: 17.6vw;
  border: none;
  background-color: transparent;
  outline: none;
}

.toastStyle {
  min-width: 40vw;
}
</style>