<template>
  <!-- <div id="nav">
      <router-link to="/notfind">Home</router-link> |
      <router-link to="/ios">About</router-link>
      <router-link to="/android">About</router-link>
    </div> -->
  <div id="app">
    <router-view />
  </div>
</template>
<script>
export default {
  data() {
    return {
      parentDPmub: "",
    };
  },
  methods: {},
  created() {
    console.log("app");
    window.addEventListener("load", () => {
      if (window.location.href.split("?")[1]) {
        const params = window.location.href.split("?")[1].split("&");
        const obj = {};
        params.map((v) => (obj[v.split("=")[0]] = v.split("=")[1]));
        this.parentDPmub = obj["myDPmub"];
      }
      if (this.$route.path !== "/") {
        // 切换到首页
        this.$router.replace({
          name: "home",
          query: {
            parentDPmub: this.parentDPmub,
          },
        });
      }
    });
  },
};
</script>
<style>
* {
  padding: 0;
  margin: 0;
}
#app {
  text-align: center;
  color: #2c3e50;
  position: relative;
}
</style>
